.btn{display:flex;align-items:center;background-color:#ffc500;padding:5px 5px 5px 12px;border-radius:32px;font-weight:bold;border:none;font-size:14px;background-repeat:no-repeat;background-position:-120px -120px, 0 0;background-image:linear-gradient(to top left, rgba(255,255,255,0.2) 0%, rgba(255,255,255,0.2) 37%, rgba(255,255,255,0.8) 45%, rgba(255,255,255,0) 50%);background-size:250% 250%, 100% 100%;transition:all 0s ease}.btn .icon{background-color:#fff;border-radius:48px;margin-left:12px;width:32px;height:32px;display:flex;align-items:center;justify-content:center}.btn:hover{background-position:0 0, 0 0;transition-duration:0.5s;box-shadow:0 1px 1px rgba(0,0,0,0.11),0 2px 2px rgba(0,0,0,0.11),0 4px 4px rgba(0,0,0,0.11),0 8px 8px rgba(0,0,0,0.11),0 16px 16px rgba(0,0,0,0.11),0 32px 32px rgba(0,0,0,0.11)}.btn:active{box-shadow:none}

